.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #000;
}

.cell {
  width: 15px;
  height: 15px;
  border-top: 1px solid #333;
  border-left: 1px solid #222;
  background-color: #000;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.active {
  background-color: #fff;
}

.intro {
  background-color: #000;
  -webkit-box-shadow: 10px 5px 5px #000;
          box-shadow: 10px 5px 5px #000;
  color: #fff;
  border-radius: 10px;
  pointer-events: none;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.intro.show {
  opacity: 0.8;
  visibility: visible;
}

.intro > ul {
  font-size: 16px;
  padding: 30px;
}

.generation {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #333;
  opacity: 0.3;
  font-size: 40vw;
  pointer-events: none;
}

span.red { 
  color: red;
}



